body { overflow: hidden; }
#tour { display: none !important; }

#authorization-container {
  z-index: 99999;
  height: 100vh;
  width: 100vw;
  display: grid;
  background: white;
  align-content: center;
  justify-content: center;
  position: fixed;
  overflow: none;
  top: 0;

  #user-login, #registration {
    background: lightgray;
    border: 1px solid gray;
    min-width: 470px;
    padding: 14px 25px;
    text-align: center;

    img {
      max-height: 200px;
      width: auto;
    }

    img, input { margin-bottom: 17px; }

    input, button {
      display: block;
      padding: 10px;
      width: 100%;
      border-radius: 0;
      border: 1px solid lightgray;
    }

    button {
      padding: 11px 0;
      background: #098f09;
      border: 1px solid gray;
      text-transform: inherit;

      &:hover { background: green; }
    }

    button[type="button"] {
      background: #f3f3f3;
      color: black;

      &:hover { background: #ebebeb; }
    }
  }

  #registration button[type="button"] {
    background: transparent;
    border: 0;
    margin-top: 15px;
    font-size: 15px;

    &:hover {
      background: transparent;
      text-decoration: underline;
    }
  }

  #buttons-container, #password-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15px;
  }

  &.loading #user-login, &.loading #registration,
  &.user-login #registration, &.registration #user-login { display: none; }
}

#xml-container {
  display: inline-block;
  margin-left: 14px;
  cursor: pointer;
  padding: 4px 7px 0px 7px;
  background: white;
  border: 1px solid lightgray;

  #xml-items {
    display: none;
    position: fixed;
    background: white;
    border: 1px solid lightgray;
    margin-top: -1px;
    font-size: 14px;
    margin-left: -8px;

    span {
      padding: 8px;
      display: block;

      &.current, &:hover {
        background: lightgray;
        color: gray;
      }

      &.current { cursor: default; }
    }
  }

  &:hover {
    #xml-items {
      display: block;
    }
  }
}

#topbar, #toolbox, .dashboard {
  position: absolute;
  left: 210px;
  width: calc(100% - 210px);
}

#topbar, #toolbox { z-index: 9; }
#topbar { top: 0px; }
#toolbox { top: 23px; }

#header {
  position: absolute;
  height: auto;
  width: auto;
  padding: 0;
  border: 0;
  right: 10px;
  z-index: 99;
  top: 26px;
}

#login {
  margin: 0;
  border-radius: 0;
}

#blocklyDiv .blocklyToolboxDiv:first-child img {
  margin-top: 10px;
  max-width: 190px;
}

#blocklyDiv .blocklyToolboxDiv:first-child div {
  padding: 13px 20px;
  font-size: 14px;

  &#user-data {
    color: black;

    img {
      width: 70px;
      border-radius: 100%;
      margin-bottom: 10px;
    }

    div { padding: 0; }
  }

  &.side-button {
    text-align: start;
    font-size: 16px;
    color: black;
    cursor: pointer;

    &.active, &:hover {
      color: gray;
      background: lightgray;
    }

    &.active { cursor: default; }
  }

  a {
    text-decoration: none;
    font-weight: normal;
    width: 100%;
    height: 100%;
    display: block;
  }

  &#user-logout {
    color: #b90202;

    &:hover {
      background: #b90202;
      color: white;
    }
  }

  &#sign-in:hover, &#contact:hover {
    background: #0d0da5;
    a { color: white; }
  }
}

.dashboard {
  display: none;
  z-index: 999;
  padding: 20px;
  height: 100vh;
  background: white;
  top: 0px;

  &.active { display: block; }
}


#tutorials-container {
  display: grid;
  grid-template-rows: min-content auto;
  height: 100%;

  header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    border: 1px solid gray;
    background: lightgray;

    div {
      cursor: pointer;
      padding: 10px;
      &:hover, &.active-tutorial { background: gray; }
    }
  }

  main {
    overflow-y: scroll;
    height: 100%;

    section {
      display: none;

      &.active-tutorial { display: block; }

      .iframe-container {
        width: 100%;
        margin-bottom: 12px;
        height: calc(100vh - 80px);

        &:last-child { margin-bottom: 0; }

        iframe {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}

#main-logout { height: 0; }

#main-account {
  border: 0;
  background-color: transparent;
}

.account { font-size: 12px !important; }

.main-account div {
  display: inline;
  color: black;
}

.main-nav .nav-caret {
  top: -25px;
  right: -95px;
  color: black;
}

@import 'extension_garixbot';
